import React, { useState } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import uuid from 'uuid/v1';
import Icon from '../Icons/Icon';

const SectionName = styled.button`
  position: relative;
  display: flex;
  align-items: center;
  border: none;
  background: none;
  width: 100%;
  text-align: center;
  border-left: 4px solid ${prop => (prop.open ? '#ff6e7e' : '#fff')};
  font-family: MarkProBold;
  font-size: 16px;
  padding: 12px 0 12px 16px;
  line-height: 24px;
  border-bottom: ${prop => (prop.open ? 0 : '1px')} solid #d1d7da;
  text-transform: none;
  color: ${prop => (prop.open ? '#191919' : '#323232')};

  &:hover,
  &:focus {
    background: none;
    color: inherit;
  }

  .cta-label {
    display: inline-block;
    margin-left: 10px;
  }

  .lux-lite-icon.arrow-down,
  .lux-lite-icon.arrow-up {
    position: absolute;
    right: 20px;
    top: 13px;
  }
`;

const Nav = styled.nav`
  display: ${prop => (prop.open ? 'block' : 'none')};
  border-bottom: 1px solid #d1d7da;
  padding: 0 0 0.75rem;

  .link {
    display: block;
    color: #323232;
    padding: 0.75rem 0 0.75rem 3.4375rem;
    line-height: 24px;
    font-family: MarkProBold;
    text-decoration: none;
  }
`;

const MegaNavSection = ({ icon, label, items }) => {
  const [open, setOpen] = useState(true);
  const id = uuid();
  return (
    <section>
      <SectionName
        aria-controls={id}
        aria-expanded={open}
        aria-label={label}
        open={open}
        onClick={() => setOpen(!open)}
      >
        <Icon name={icon} />
        <span className="cta-label">{label}</span>
        <Icon size={16} name={open ? 'arrow-up' : 'arrow-down'} />
      </SectionName>
      <Nav id={id} open={open}>
        {items &&
          items.map(({ label, link }, index) => {
            return (
              <a className="link" key={index} href={link}>
                {label}
              </a>
            );
          })}
      </Nav>
    </section>
  );
};

MegaNavSection.propTypes = {
  icon: PropTypes.string,
  label: PropTypes.string,
  items: PropTypes.arrayOf(PropTypes.object)
};

export default MegaNavSection;
