import React from 'react';
import { useAppContext } from '../AppContext';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import MegaNavSectionPhone from './MegaNavSectionPhone';
import useOnClickOutside from '../../helpers/hooks/useOnClickOutside';

const Wrapper = styled.div`
  position: absolute;
  z-index: 2;
  top: 84px;
  width: 100%;
  left: 0;
  padding: 0px;
`;

const GridContainer = styled.div`
  box-shadow: 0 0.125rem 0.375rem 0 rgba(0, 0, 0, 0.3);
  background: #fff;
`;

const MegaNavPhone = ({ sections = [] }) => {
  const ref = React.useRef(null);
  const [{ showMegaNav }] = useAppContext();
  const [, appContextDispatcher] = useAppContext();

  useOnClickOutside(ref, () =>
    appContextDispatcher({ payload: { showMegaNav: false } })
  );

  if (!showMegaNav) {
    return null;
  }

  return (
    <Wrapper>
      <GridContainer ref={ref}>
        {sections.length &&
          sections.map(({ items, label, icon }, index) => {
            return (
              <MegaNavSectionPhone
                key={index}
                icon={icon}
                items={items}
                label={label}
              />
            );
          })}
      </GridContainer>
    </Wrapper>
  );
};

MegaNavPhone.propTypes = {
  sections: PropTypes.arrayOf(PropTypes.object)
};

export default MegaNavPhone;
